.heading {
    width: 100%;
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
}

.title {
    font-size: 30px;
    font-weight: 300;
    margin: 0;
    padding-left: 15px;;
}

@media (max-width: 499px) {
    .heading {
        flex-direction: column;
    }
}